@charset "utf-8";
.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.l_flex {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.yuQingContainer {
  height: 100%;
  min-width: 19.2rem;
  min-height: auto;
  padding: 0.43rem 0.38rem;
  background-color: #1B418D;
  box-sizing: border-box;
  color: #ffffff;
  overflow: hidden;
}
.yuQingContainer .conter {
  width: 100%;
  margin-top: 0.14rem;
  height: calc(100% - .92rem);
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  align-items: flex-start;
}
.yuQingContainer .conter .flatAngle {
  background-color: #305297;
}
.yuQingContainer .conter .conter-left {
  width: 40%;
  background: url(../images/forense-left.png) no-repeat;
}
.yuQingContainer .conter .conter-left .conterLeft-bottom-item {
  font-size: 0.18rem;
}
.yuQingContainer .conter .conter-left .conterLeft-bottom-item .sort-list {
  cursor: pointer;
  height: 0.84rem;
  padding: 0.12rem 0.2rem 0.1rem 0.18rem;
}
.yuQingContainer .conter .conter-left .hostSort.img {
  width: 0.42rem;
  height: 0.59rem;
  margin-right: 0.19rem;
}
.yuQingContainer .conter .conter-left .hostSort.sort0 {
  background: url('../images/sort1.png') no-repeat;
  background-size: 100% 100%;
}
.yuQingContainer .conter .conter-left .hostSort.sort1 {
  background: url('../images/sort2.png') no-repeat;
  background-size: 100% 100%;
}
.yuQingContainer .conter .conter-left .hostSort.sort2 {
  background: url('../images/sort3.png') no-repeat;
  background-size: 100% 100%;
}
.yuQingContainer .conter .conter-left .hostSort.number {
  display: block;
  width: 0.48rem;
  height: 0.48rem;
  text-align: center;
  line-height: 0.48rem;
  margin-right: 0.16rem;
  font-size: 0.3rem;
  background-color: #4C91F5;
  border-radius: 0.06rem;
}
.yuQingContainer .conter .conterLeft-top {
  height: 3.53rem;
}
.yuQingContainer .conter .conterLeft-bottom {
  height: 100%;
}
.yuQingContainer .conter .conter-middle {
  margin: 0 0.06rem;
  width: 60%;
  height: 100%;
  background: url(../images/forense-middle.png) no-repeat;
}
.yuQingContainer .conter .conter-right {
  width: 40%;
  height: 100%;
}
.yuQingContainer .conter .conter-right .radiusBox-bottom,
.yuQingContainer .conter .conter-right .radiusBox-top {
  padding: 0.22rem;
  height: 50%;
  background: url('../images/forense-right.png') no-repeat;
  background-size: 100% 100%;
}
.yuQingContainer .conter .conter-right .radiusBox-top {
  padding-bottom: 0.12rem;
  margin-bottom: 0.08rem;
}
.yuQingContainer .conter .conter-right .radiusBox-bottom {
  padding-top: 0.12rem;
}
.yuQingContainer .conter .sortConter {
  flex-direction: column;
}
.yuQingContainer .conter .sortConter-top {
  width: 100%;
}
.yuQingContainer .conter .sortConter-bottom {
  width: 100%;
  color: #81A0D3;
}
.yuQingContainer .conter .sortConter-host {
  color: #fdd835;
}
.yuQingContainer .conter .sortConter-host i {
  color: #F3Af1B;
}
.yuQingContainer .conter .conter-left,
.yuQingContainer .conter .conter-middle,
.yuQingContainer .conter .conter-right {
  height: 100%;
  padding: 0.22rem;
  background-size: 100% 100%;
}
.yuQingContainer .conter .conter-right {
  padding: 0;
}
.yuQingContainer .conter .iconlujing1 {
  font-size: .24rem;
}
#app {
  padding: 0 0.22rem;
}
#app .flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#app .flexStatr {
  display: flex;
  align-items: center;
}
#app .flexAround {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#app .navbar-header {
  width: 100%;
  height: 0.92rem;
  padding: 0.22rem 0.22rem 0.19rem 0.4rem;
  box-sizing: border-box;
  background-color: #2551A2;
}
#app .navbar-header h2 {
  font-size: 0.58rem;
}
#app .navbar-title {
  height: 100%;
}
#app .navbar-title .navbarIcon {
  width: 0.46rem;
  font-size: 0.46rem;
  cursor: pointer;
  color: #F3Af1B;
}
#app .navbar-right {
  height: 100%;
}
#app .navbar-line {
  display: block;
  height: 100%;
  width: 1px;
  margin: 0 0.2rem 0 0.4rem;
  background-color: #4E74B5;
}
#app .navbar-time {
  font-size: 0.24rem;
}
#app .timer-week {
  margin: 0 0.32rem 0 0.14rem;
}
#app .time-hours {
  font-size: 0.38rem;
  color: #F3Af1B;
  margin-right: 0.36rem;
}
#app .navbar-user {
  font-size: 0.28rem;
}
#app .navbar-user .userImg {
  width: 0.26rem;
  margin-right: 0.12rem;
}
#app .rightIcon {
  position: relative;
  cursor: pointer;
  font-size: 0.3rem;
  margin-left: 0.2rem;
}
#app .rightIcon:hover .topTooltip {
  display: block;
}
#app .rightIcon .topTooltip {
  display: none;
  padding: 0.1rem 0.18rem;
  font-size: 0.16rem;
  color: #000;
  position: absolute;
  white-space: nowrap;
  bottom: 110%;
  left: 50%;
  border-radius: 0.06rem;
  transform: translateX(-50%);
  background: #f3af1b;
  box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.16);
}
#app .rightIcon .topTooltip::after {
  content: '';
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  background-color: #f3af1b;
  transform: rotate(45deg);
  position: absolute;
  left: 45%;
  z-index: -1;
  bottom: -12%;
}
#navbar-right {
  position: absolute;
  left: 0;
  margin-top: 0.04rem;
  width: 0.6rem;
}
#navbar-right:hover .navbar-big {
  display: block;
}
#navbar-right .navbar-ul {
  margin-top: 0.34rem;
}
#navbar-right .navbar-ul li {
  font-size: 0.42rem;
  color: #F3Af1B;
  margin-bottom: 0.8rem;
  text-align: center;
  width: 0.44rem;
  height: 0.44rem;
  line-height: 0.44rem;
  cursor: pointer;
}
#navbar-right .navbar-ul li:hover,
#navbar-right .navbar-ul li:active,
#navbar-right .navbar-ul li.active {
  color: #5AB0F3;
}
#navbar-right .navbar-big {
  position: absolute;
  display: none;
  left: 100%;
  top: 0;
  width: 1.2rem;
  z-index: 9;
  color: #5AB0F3;
}
#navbar-right .navbar-big li {
  width: 100%;
  background-color: #052D7C;
  height: 1.2rem;
  text-align: center;
  cursor: pointer;
}
#navbar-right .navbar-big li span {
  display: inline-block;
  margin: 0.24rem auto 0.15rem;
}
#navbar-right .navbar-big li:hover,
#navbar-right .navbar-big li:active,
#navbar-right .navbar-big li.active {
  color: #F3Af1B;
  background-color: #305297;
}
.comTitleBox {
  display: flex;
  height: 0.78rem;
  align-items: center;
  position: relative;
}
.comTitleBox .incnEdit {
  position: absolute;
  right: 0.18rem;
}
.modulTitle {
  height: 100%;
  line-height: 0.78rem;
  font-size: 0.22rem;
}
.modulTitle i {
  font-size: 0.32rem;
  color: #F3Af1B;
  margin: 0 0.18rem 0 0.26rem;
}
